<template>
	<view class="home" v-if="home">
		<!-- 状态栏 -->
		<view class="status" :style="{ position: headerPosition,top:statusTop,opacity: afterHeaderOpacity}"></view>
		<!-- 顶部导航栏 -->
		<view class="header" :style="{ position: headerPosition,top:headerTop,opacity: afterHeaderOpacity }">
			<!-- 定位城市 -->
			<view class="addr">
				<view class="icon location"></view>
				{{ city }}
			</view>
			<!-- 搜索框 -->
			<view class="input-box">
				<input placeholder="搜索商品名称" placeholder-style="color:#c0c0c0;" @tap="toSearch()" />
				<view class="icon search"></view>
			</view>
			<!-- 右侧图标按钮 -->
			<view class="icon-btn">

				<view class="hongdian"></view>

				<view class="hongdian" v-show="msg"></view>

				<view class="icon tongzhi" @tap="toMsg"></view>
			</view>
		</view>
		<!-- 占位 -->
		<view class="place"></view>
		<!-- 轮播图 -->
		<view class="swiper">
			<view class="swiper-box">
				<swiper circular="true" autoplay="true" @change="swiperChange">
					<swiper-item v-for="(swiper,index) in swiperList" :key="swiper.id" current="index">
						<image :src="swiper.cover" @tap="toSwiper(swiper,index)"></image>
					</swiper-item>

				</swiper>
				<view class="indicator">
					<view>{{currentSwiper + 1}} </view>
					<view> / {{swiperList.length}}</view>
				</view>
			</view>
		</view>
		<!-- 分类列表 -->
		<view class="category-list">
			<view class="category" v-for="(row, index) in categoryList" :key="index" @tap="toCategory(row)">
				<view class="img">
					<image :src="row.logo"></image>
				</view>
				<view class="text">{{ row.title }}</view>
			</view>
		</view>
		<view class="bj-color"></view>
		<!-- 广告图 -->
		<view class="banner">
			<image src="../../static/img/category/jijian.png"></image>
		</view>
		<!-- 活动区 -->
		<view class="promotion">
			<view class="promotion-head">
				<view class="promotion-head-left">
					<h2 class="title">限时抢购</h2>
					<view class="english">
						<text>FLASH</text>
						<text>SALES</text>
					</view>
				</view>
			</view>
			<view class="hot-list">
				<view class="hot-list-item" v-for="(row, index) in limitList" @tap="toGoods(row)" :key="index">
					<view class="item-img">
						<image :src="row.logo" />
					</view>
					<view class="item-title">{{row.title}}</view>
					<view class="item-price">
						<text>{{row.price}}</text>
						<text>{{row.market_price}}</text>
					</view>
				</view>
			</view>
		</view>
		<!-- 热销产品 -->
		<view class="hot promotion">
			<view class="promotion-head">
				<view class="promotion-head-left">
					<h2 class="title">热销产品</h2>
					<view class="english">
						<text>HOT-SALE</text>
						<text>PRODUCT</text>
					</view>
				</view>
				<view class="promotion-head-right" @click="moreLoad">
					更多
				</view>
			</view>
			<view class="hot-list">
				<view class="hot-list-item" v-for="(row, index) in hotList" @tap="toGoods(row)" :key="index">
					<view class="item-img">
						<image :src="row.logo" />
					</view>
					<view class="item-title">{{row.title}}</view>
					<view class="item-price">
						<text>{{row.price}}</text>
						<text>{{row.market_price}}</text>
					</view>
				</view>
			</view>
		</view>
		<!-- 商品列表 -->
		<view class="goods-list">
			<view class="title">

				为你推荐

			</view>
			<view class="product-list">
				<view class="product" v-for="(product,index) in productList" :key="index" @tap="toGoods(product)">
					<image mode="widthFix" :src="product.logo"></image>
					<view class="name">{{ product.title }}</view>
					<view class="info">
						<view class="price">{{ product.price }}</view>
						<view class="slogan">{{ product.market_price }}</view>
					</view>
				</view>
			</view>
			<!-- <view class="loading-text" v-show="tishi">{{ loadingText }}</view> -->
		</view>

		<!-- <uni-load-more :status="more" :showIcon="showIcon"></uni-load-more> -->

		<uni-load-more :status="status" :showIcon="showIcon"></uni-load-more>

	</view>
</template>

<script>
	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"


	//高德SDK
	import amap from '../../common/SDK/amap-wx.js';
	export default {
		components: {
			uniLoadMore
		},
		mounted() {
				 this.init();	
				
		},

		data() {
			return {
				msg: "", //系统消息显示
				msgList: [], //系统信息列表
				showIcon: false,
				status: "more",
				afterHeaderOpacity: 1, //不透明度
				headerPosition: 'fixed',
				headerTop: null,
				statusTop: null,
				city: '北京',
				currentSwiper: 0,
				// 轮播图片
				swiperList: [],
				// 分类菜单
				categoryList: [],
				Promotion: [],
				// 热销产品
				hotList: [],
				//限制产品
				limitList: [],
				//猜你喜欢列表
				productList: [],
				totalList: [],
				loadingText: '正在加载...',
	            token1:"",
				
				current_page: 0,
				total: "",
				last_page: "1",
				tishi: false,
				hot:[],
				home:false
			}
		},
		onPageScroll(e) {
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
			this.headerTop = e.scrollTop >= 0 ? null : 0;
			this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
		onReachBottom() {
			// 调用获取推荐列表接口
			// 当前页小于最后一页才调用
			if (this.current_page < this.last_page) {
				this.getRecommendList();
			}

		},
		onReady(){
			 //  uni.showLoading({
			 // 	title: '加载中'
			 // });
		},
		onLoad() {
			uni.getStorage({
					key:"info",
					success: (res) => {
						
						var result=JSON.parse(res.data)
						
						this.token1=result.token;
						console.log(res,result.token,this.token1)
						this.first_load();
						
					}
				})
		
		},
		methods: {
			//加载更多热销产品
			moreLoad(){
				this.hotList=this.hot;
			},
			init(){
				uni.hideLoading();
				
					
			},
			first_load(){
				
				
				var amapPlugin = new amap.AMapWX({
					//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
					key: '5b9b64be2413fc19c26683fcf0de890f'
				});
				//定位地址
				amapPlugin.getRegeo({
					success: data => {
						//console.log(data)
						this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
					    this.init()
					}
				});
				
				//系统消息
				uni.request({
					url: this.config.url + "member/message",
					method: "POST",
					data: {
						token: this.token1
					},
					success: (res) => {
						//console.log("mesg",res)
						
						if (res.data.code == 1) {
							this.msgList = res.data.data.data;
							if (res.data.data.data.length > 0) {
								this.msg = true;
							}
						}
					}
				})
				
					uni.request({
						url: this.config.url+'home', //仅为示例，并非真实接口地址。
						data: {
							token: this.token1
						},
						method: "post",
						success: (res) => {
					     //   console.log("hoem", res);
							var num;
							if(res.data.code==1){
							//	console.log(res)
								this.swiperList = res.data.data.banner;
								this.categoryList = res.data.data.cate;
								
								this.limitList = res.data.data.limit_buy;
								this.hot = res.data.data.hot;
								 this.home=true;
								 
								
								if (this.hotList.length < 10) {
									num = this.totalList.length;
								} else {
									num = 10;
								}
								for (var i = 0; i < num; i++) {
									this.hotList.push(this.hot[i])
									//	console.log(this.productList)
								}
							  
							}
							
						}
					})
				
				
			},
			
			
			// 获取推荐列表
			getRecommendList() {
				this.status = "loading";
				// 首页为你推荐
				uni.request({
					url: this.config.url+'recommend', //仅为示例，并非真实接口地址。
					data: {
						token: this.token1,
						page: Number(this.current_page) + 1,
					},
					method: "post",
					success: (res) => {
						var len;
						// console.log("res.data", res.data);
						// 商品列表
						this.totalList = res.data.data.data;
						//每页10 
						if (this.totalList.length < 10) {
							len = this.totalList.length;
						} else {
							len = 10;
						}
						for (var i = 0; i < len; i++) {
							this.productList.push(this.totalList[i])
							//	console.log(this.productList)
						}

						this.current_page = res.data.data.current_page;
						this.last_page = res.data.data.last_page;
						this.total = res.data.data.data.total;
						this.status = "more";
						if (this.current_page >= this.last_page) {
							this.status = "noMore";
						}
					}
				});

			},


			//消息列表
			toMsg() {
				uni.navigateTo({
					url: '/pages/msg/msg'
				})
			},
			//搜索跳转
			toSearch() {
				// 搜索推荐
				uni.request({
					url: this.config.url+'keyword', //仅为示例，并非真实接口地址。
					data: {
						token: this.token1

					},
					method: "post",
					success: (res) => {

						console.log(res);
						this.searchList = res.data.data;
					}
				});

			},
			//轮播图跳转
			toSwiper(e, index) {

				//this.currentSwiper = index
			},
			//分类跳转
			toCategory(e) {
				console.log(e);
				uni.navigateTo({
					url:"/pages/goods/goods-list?id="+e.id+"&title="+e.title
				})
			},
			//商品跳转
			toGoods(e) {
				uni.navigateTo({
					url: "/pages/goods/goods?id="+e.id
				})
			},
			//轮播图指示器
			swiperChange(event) {
				this.currentSwiper = event.detail.current;
			}
		}
	}
</script>
<style lang="scss">
	page {
		position: relative;
	}

	@font-face {
		font-family: 'HMfont-home';
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}

	.icon {
		font-family: 'HMfont-home' !important;
		font-size: 60upx;
		font-style: normal;
		color: #000000;

		&.yuyin {
			&:before {
				content: '\e64e';
			}
		}

		&.tongzhi {
			&:before {
				content: '\e729';
			}
		}

		&.search {
			&:before {
				content: '\e628';
			}
		}

		&.location {
			&:before {
				content: '\e611';
			}
		}

		&.xia {
			&:before {
				content: '\e689';
			}
		}
	}

	.home {}

	.hot .promotion-head-right {
		font-size: 24upx;
		font-family: PingFang-SC-Regular;
		font-weight: 400;
		color: rgba(153, 153, 153, 1);
	}

	.pullDown-effects {
		position: fixed;
		//top: calc(100upx - 36vw);
		top: 0;
		z-index: 9;
		width: 100%;
		height: 36vw;
		/*  #ifdef  APP-PLUS  */
		padding-top: var(--status-bar-height);

		/*  #endif  */
		image {
			width: 100%;
			height: 36vw;
		}
	}

	.status {
		width: 100%;
		height: 0;
		position: fixed;
		z-index: 10;
		background-color: #fff;
		top: 0;
		/*  #ifdef  APP-PLUS  */
		height: var(--status-bar-height); //覆盖样式
		/*  #endif  */
	}

	.header {
		width: 92%;
		padding: 0 4%;
		height: 100upx;
		display: flex;
		align-items: center;
		position: fixed;
		top: 0;
		z-index: 10;
		background-color: #fff;

		/*  #ifdef  APP-PLUS  */
		top: var(--status-bar-height);
		/*  #endif  */

		.addr {
			width: 120upx;
			height: 60upx;
			flex-shrink: 0;
			display: flex;
			align-items: center;
			font-size: 28upx;

			.icon {
				height: 60upx;
				margin-right: 5upx;
				display: flex;
				align-items: center;
				font-size: 42upx;
				color: #ffc50a;
			}
		}

		.input-box {
			width: 100%;
			height: 60upx;
			background-color: #f5f5f5;
			border-radius: 30upx;
			position: relative;
			display: flex;
			align-items: center;

			.icon {
				display: flex;
				align-items: center;
				position: absolute;
				top: 0;
				left: 20upx;
				width: 60upx;
				height: 60upx;
				font-size: 34upx;
				color: #c0c0c0;
			}

			input {
				padding-left: 70upx;
				height: 28upx;
				font-size: 28upx;
			}
		}

		.icon-btn {
			width: 120upx;
			height: 60upx;
			flex-shrink: 0;
			display: flex;
			justify-content: center;
			position: relative;

			.hongdian {
				width: 10upx;
				height: 10upx;
				border-radius: 50%;
				background: red;
				position: absolute;
				top: 7upx;
				right: 30upx;
			}

			.icon {
				width: 60upx;
				height: 60upx;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				font-size: 42upx;
			}
		}
	}

	.place {
		background-color: #ffffff;
		height: 100upx;
		/*  #ifdef  APP-PLUS  */
		margin-top: var(--status-bar-height);
		/*  #endif  */
	}

	.swiper {
		width: 100%;
		margin-top: 10upx;
		display: flex;
		justify-content: center;

		.swiper-box {
			width: 100%;
			height: 352upx;

			overflow: hidden;


			//兼容ios，微信小程序
			position: relative;
			z-index: 1;

			swiper {
				width: 100%;
				height: 352upx;

				swiper-item {
					image {
						width: 100%;
						height: 352upx;
					}
				}
			}

			.indicator {
				position: absolute;
				bottom: 20upx;
				left: 20upx;
				width: 102upx;
				height: 46upx;
				background: rgba(0, 0, 0, 0.5);

				border-radius: 46upx;
				overflow: hidden;
				display: flex;
				justify-content: center;
				align-items: center;

				view: {
					display: flex;
					justify-content: center;
					align-items: center;
				}

				view:nth-child(1) {
					color: #00C65D;
					font-size: 30upx;
					amrgin-right: 2upx;
				}

				view:nth-child(2) {
					color: white;
					font-size: 28upx;
					margin-left: 5upx;
				}
			}
		}
	}

	.category-list {
		width: 92%;
		margin: 0 4% 20upx;
		padding: 0 0 30upx 0;
		border-bottom: solid 2upx #f6f6f6;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		.category {
			width: 20%;
			margin-top: 50upx;
			display: flex;
			flex-wrap: wrap;

			.img {
				width: 100%;
				display: flex;
				justify-content: center;

				image {
					width: 9vw;
					height: 9vw;
				}
			}

			.text {
				margin-top: 16upx;
				width: 100%;
				display: flex;
				justify-content: center;
				font-size: 24upx;
				color: #3c3c3c;
			}
		}
	}

	.bj-color {
		width: 100%;
		height: 20upx;
		background: rgba(247, 247, 247, 1);
	}

	.banner {
		width: 100%;
		margin: 43upx 0 58upx;

		image {
			width: 100%;
			height: 144upx;

			box-shadow: 0upx 5upx 25upx rgba(0, 0, 0, 0.3);
		}
	}

	// 活动区 
	.promotion {
		width: 92%;
		margin: 0 4%;

		.text {
			width: 100%;
			height: 60upx;
			font-size: 34upx;
			font-weight: 600;
			margin-top: -10upx;
		}

		.promotion-head {
			margin: 50upx 0;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.promotion-head-left {
				display: flex;
				align-items: center;
				justify-content: flex-start;

				.title {
					font-size: 44upx;
					font-family: PingFang-SC-Bold;
					font-weight: 700;
					color: rgba(16, 16, 16, 1);
					height: 100%;
				}

				.english {
					margin-left: 5upx;
					display: flex;
					flex-direction: column;
					-webkit-transform: scale(1);
					transform: scale(1);

					text {
						font-size: 14upx;
						font-family: PingFang-SC-Medium;
						font-weight: 500;
						color: rgba(85, 85, 85, 1);
					}
				}
			}

		}

		.list {
			width: 100%;
			display: flex;
			justify-content: space-between;

			.column {
				width: 43%;
				padding: 15upx 3%;
				background-color: #ebf9f9;
				border-radius: 10upx;
				overflow: hidden;
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;

				.top {
					width: 100%;
					height: 40upx;
					display: flex;
					align-items: center;
					margin-bottom: 5upx;

					.title {
						font-size: 30upx;
					}

					.countdown {
						margin-left: 20upx;
						display: flex;
						height: 40upx;
						display: flex;
						align-items: center;
						font-size: 20upx;

						view {
							height: 30upx;
							background-color: #f06c7a;
							display: flex;
							justify-content: center;
							align-items: center;
							color: #fff;
							border-radius: 4upx;
							margin: 0 4upx;
							padding: 0 2upx;
						}
					}
				}

				.left {
					width: 50%;
					height: 18.86vw;
					display: flex;
					flex-wrap: wrap;
					align-content: space-between;

					.ad {
						margin-top: 5upx;
						width: 100%;
						font-size: 22upx;
						color: #acb0b0;
					}

					.into {
						width: 100%;
						font-size: 24upx;
						color: #aaa;
						margin-bottom: 5upx;
					}
				}

				.right {
					width: 18.86vw;
					height: 18.86vw;

					image {
						width: 18.86vw;
						height: 18.86vw;
					}
				}
			}
		}
	}

	.hot-list {
		width: 100%;
		display: flex;

		flex-wrap: wrap;

		.hot-list-item {
			width: 33.3%;

			display: flex;
			flex-direction: column;

			flex-wrap: wrap;
			padding-bottom: 20upx;

			.item-img {
				width: 224upx;
				height: 224upx;
				display: flex;
				justify-content: center;

				image {
					width: 100%;
					height: 100%;

				}
			}

			.item-title {
				margin: 20upx 0;
				font-size: 26upx;
				font-family: PingFang-SC-Regular;
				font-weight: 400;
				color: rgba(16, 16, 16, 1);
				width: 100%;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}

			.item-price {
				dispaly: flex;
				justify-content: flex-start;
				align-items: center;

